.winup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
  overflow-y: auto;
  overflow-x: hidden;
}
.winup:target {
  opacity: 1;
  visibility: visible;
}

.winup:target .winup__content {
  transform: translate(0px, 0px);
  opacity: 1;
}

.winup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.winup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}

.winup__content {
  position: relative;
  padding: 10px;

  background-color: white;
  color: black;
  border-radius: 15px;
  opacity: 0;
  transform: translate(-100%, 0px);
  transition: all 0.8s ease 0s;

  @media screen and (min-width: 768px) {
    width: 628px;
  }

  @media screen and (min-width: 1024px) {
    width: 900px;
  }
}

.button {
  position: absolute;
  right: 0;
  top: 15px;
  font-size: 20px;
  color: black;
  stroke: 4px;
  width: 35px;
  height: 30px;

  &:hover {
    color: #ff6b08;
  }
}

img {
  display: inline;
  // transform: scale(0.9);
}

.team__list {
  padding-bottom: 60px;
  justify-content: end;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5%;
  grid-row-gap: 2%;

  @media screen and (min-width: 767px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media screen and (min-width: 1024px) {
    grid-template-columns: repeat(4, 1fr);
    grid-row-gap: 4%;
  }
}

.title-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .title-wrapper {
    margin-bottom: 30px;
    margin-top: 30px;
  }
}

@media screen and (min-width: 1024px) {
  .title-wrapper {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

.team__title {
  font-size: 20px;

  &:hover {
    color: #ff6b08;
  }

  @media screen and (min-width: 768px) and (max-width: 1023px) {
    line-height: 1.16;
    margin-bottom: 20px;
    @media screen and (min-width: 1024px) {
      margin-top: 10px;
      margin-bottom: 40px;
      font-size: 28px;
    }
  }
}

.under {
  color: #ff6b08;
  font-weight: bold;
  font-size: 18px;
}

.goitLogo {
  height: 24px;
  width: 65px;
  margin-right: 10px;

  @media screen and (min-width: 768px) and (max-width: 1023px) {
    height: 23px;
    width: 65px;
    margin-right: 15px;
  }

  @media screen and (min-width: 1024px) {
    height: 24px;
    width: 80px;
    margin-right: 15px;
  }
}

.team {
  border-radius: 15px;
  margin-bottom: 5%;
  justify-content: center;
}

.team-discription {
  font-weight: bold;
  margin-top: 3%;
  margin-bottom: 3%;
}
